.sidebar {
  width: 250px;
  height: 100vh;
  background-color: #1f2937; /* Gris oscuro moderno */
  color: white;
  position: fixed;
  top: 0;
  left: 0;
  padding-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  transition: transform 0.3s ease;
  z-index: 1500;
  padding-left: 20px;
  box-shadow: 2px 0 8px rgba(0,0,0,0.2);
}

.sidebar h3 {
  margin-bottom: 0px;
  font-size: 1.4rem;      /* Ajuste del tamao */
  text-align: left;      /* Centrar texto */
  width: 100%;
  padding: 0;         /* Espacio interno a los lados */
  color: #f8c12c;          /* Dorado */
  word-wrap: break-word;   /* Que se divida si es muy largo */
}


.sidebar a {
  text-decoration: none;
  color: white;
  padding: 12px 15px;
  width: 100%;
  display: flex;
  align-items: center;
  font-weight: 500;
  border-radius: 5px;
  margin-bottom: 5px;
  transition: 0.3s;
}

.sidebar a:hover {
  background-color: #374151; /* Ms claro al pasar el mouse */
}

.sidebar a .icon {
  margin-right: 10px;
  font-size: 1.2rem;
}

/* Mobile */
@media (max-width: 768px) {
  .sidebar {
    transform: translateX(-100%);
  }
  .sidebar.open {
    transform: translateX(0);
  }
}

/* Contenedor del cono con badge */
.icono-noti-container {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-right: -4px;
}

/* Badge rojo con nmero */
.badge-noti {
  position: absolute;
  top: -8px;
  right: 0px;
  background-color: red;
  color: white;
  font-size: 11px;
  font-weight: bold;
  border-radius: 50%;
  padding: 2px 5px;
  line-height: 1;
  min-width: 18px;
  text-align: center;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
}

/* Link de notificacin */
.noti-link {
  display: flex;
  align-items: center;
  gap: 5px;
}

.usuario-logeado {
  font-size: 0.95rem;
  color: #b5b5b5;
  
  margin: 0px 0px 60px 0px;       /* espacio arriba y abajo */
  padding-left: 15px;   /* mismo que los links */
  width: 100%;
}
.btn-cerrar-sesion {
  background: #ff4d4d;
  border: none;
  color: white;
  padding: 10px 20px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 0.95rem;
  transition: background 0.3s ease;
  margin-left: 30px;
  margin-top: 20px;
}

.btn-cerrar-sesion:hover {
  background: #e03e3e;
  
}









